.main {
    &-screen {
        position: relative;
        line-height: 0;
        padding: 200px 0;
        background-image: url("../images/main-bg.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;

        @media(max-width:768px) {
            padding: 160px 0;
        }

        @media(max-width:420px) {
            padding-top: 100px;
            padding-bottom: 60px;
        }
    }

    &-info {
        max-width: 780px;
        line-height: 1.2;

        @media(max-width:768px) {
           max-width: none;
        }

        &-title {
            margin-bottom: 80px;
            font-size: rem(48);
            font-weight: 700;

            @media(max-width:768px) {
                margin-bottom: 50px;
                font-size: rem(32);
            }

            @media(max-width:420px) {
                margin-bottom: 40px;
                font-size: rem(24);
            }

            &-span {
                color: #3E9CDC;
                font-weight: 800;

                @media(max-width:768px) {
                    font-size: rem(32);
                }

                @media(max-width:420px) {
                    font-size: rem(24);
                }
            }
        }

        &-subtitle {
            margin-bottom: 10px;
            font-weight: 700;
            font-size: rem(20);

            @media(max-width:768px) {
                font-size: rem(16);
            }

            @media(max-width:420x) {
                font-size: rem(14);
            }
        }

        &-list {
            &-ul {
                padding-left: 25px;
                margin-bottom: 80px;

                @media(max-width:768px) {
                    margin-bottom: 20px;
                }

                
            }

            &-li {
                position: relative;
                margin-bottom: 5px;
                font-size: rem(20);
                font-weight: 400;

                @media(max-width:768px) {
                    font-size: rem(16);
                }

                @media(max-width:420px) {
                    font-size: rem(14);
                }

                &::before {
                    content: "";
                    display: inline-block;
                    position: absolute;
                    left: -20px;
                    top: 10px;
                    width: 5px;
                    height: 1.5px;
                    background-color: #000000;
                }
            }
        }

        &-btn {
            display: inline-block;
            width: 100%;
            max-width: 183px;
            height: 43px;
            margin-right: 30px;
            line-height: 41px;
            background-color: #ffffff;
            border: 1px solid #6F6D7B;
            border-radius: 4px;
            text-align: center;
            text-transform: uppercase;
            font-size: rem(13);
            font-weight: 400;
            color: #000000;
            transition: 0.3s;
            &:hover {
                opacity: 0.7;
            }

            &-blue {
                border: 1px solid #ffffff;
                background-color: #3E9CDC;
                color: #ffffff;

                @media(max-width:768px) {
                    margin-bottom: 15px;
                }
            }
        }
    }

    &-order {
        background-image: url("../images/main-order-bg.png");
        line-height: 0;
        padding-top: 120px;
        padding-bottom: 150px;
        background-position: center;
        background-size: cover;

        @media(max-width:768px) {
            padding:70px 0;
        }
    

        &-wrap {
            position: relative;
            max-width: 20%;
            padding: 0 5px;
            flex: 0 0 20%;
            text-align: center;

            @media(max-width:1230px) {
                max-width: 190px;
                flex: 0 0 190px;
            }

            @media(max-width:1023px) {
                max-width: 50%;
                flex: 0 0 50%;

                &:last-child {
                    flex: 0 0 100%;
                    max-width: 100%;
                }
            }

            @media(max-width:576px) {
                max-width: 100%;
                flex: 0 0 100%;
            }
        }

        &-title {
            margin-bottom: 30px;
            font-weight: 800;
            font-size: rem(36);
            text-align: center;
            line-height: 1.2;
            color: #ffffff;

            @media(max-width:576px) {
                font-size: rem(24);
            }

            &-span {
                color: #3E9CDC;

                @media(max-width:576px) {
                    font-size: rem(24);
                }
            }
        }

        &-subtitle {
            margin-bottom: 80px;
            font-size: rem(18);
            color: #BBBBBB;
            text-align: center;
            line-height: 1.2;

            @media(max-width:768px) {
                margin-bottom: 40px;
            }

            @media(max-width:576px) {
                font-size: rem(16);
            }
        }

        &-item {
            display: inline-block;
            position: relative;
            width:100px;
            height: 100px;
            margin-bottom: 20px;
            border-radius: 50%;
            border: 5px solid #286690;
            background-color:#3E9CDC;

            // &::before {
            //     content: "";
            //     position: absolute;
            //     background-image: url("../images/svg/main-order-dots.svg");
            //     width: 100%;
            //     height: 6px;
            // }

            &-img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                max-width: 40px;
                max-height: 40px;
            }

            &-subtitle {
                display: -webkit-box;
                height: 40px;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
                line-height: 1.2;
                color: #ffffff;

            }

           
        }

        &-row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 100px;

            @media(max-width:1023px) {
                // flex-direction: column;
                flex-wrap: wrap;
            }

            @media(max-width:768px) {
                margin-bottom: 50px;
            }

            
        
        }

        &-dots {
            display: flex;
            position: absolute;
            top: 50%;
            left: 105%;
            transform: translate(0,-50%);

            @media(max-width:1023px) {
                display: none;
            }
        }

        &-dot {
            display: inline-block;
            width: 6px;
            height: 6px;
            margin-right: 20px;
            background-color: #C4C4C4;
            border-radius: 50%;

            // @media(max-width:1260px) {
            //     &:nth-of-type(3) {
            //         display: none;
            //     }
            // }

            

            &:first-child {
                margin-left: 12px;
            }
        }

        &-input {
            position: relative;
            width: 100%;
            height: 48px;
            padding-left: 10px;
            background-color: rgba(255, 255, 255, 0.85);
            border-radius: 3px;
            color: #272733;
            font-size: rem(18);
            border: 1px solid #ffffff;
            outline: none;



            &:focus {
                border: 1px solid #3e9cdc;
            }

            &-cont {

            @media(max-width:768px) {
                margin-bottom: 20px;
            }
            }
        }

        &-select {
            @media(max-width:768px) {
                margin-bottom: 20px;
            }
        }

        &-load {
            display: flex;
            align-items: center;
            justify-content: center;
            height: 48px;
            line-height: 44px;
            width: 100%;
            border: 2px solid #3E9CDC;
            color: #272733;
            background-color: #ffffff;
            text-align: center;
            text-transform: uppercase;
            font-size: rem(18);
            font-weight: 600;
            border-radius:3px;
            cursor: pointer;
            transition: 0.3s;

            @media(max-width:1023px) {
                font-size: rem(16);
            }

            @media(max-width:768px) {
                font-size: rem(14);
            }

            &-input {
                display: none;
            }

            &:hover {
                color: #ffffff;
                // border: none;
                background-color: #3E9CDC;
            }

            img {
                margin-right: 10px;
            }
            
        }

        &-btn {
            width: 100%;
            max-width: 370px;
            height: 48px;
            line-height: 46px;
            cursor: pointer;
        }

        &-box {
            text-align: center;

            .main-order-btn {
                margin-right: 0;
            }
        }

        &-form {
            span.error {
                position: relative;
                color: #ee2742;
                line-height: 1.2;;
                // top: 11px;
            }

        }

        &-wrapper {
            display: flex;
            justify-content: space-between;
            margin-bottom: 16px;
            font-size: rem(18);
            color: #ffffff;
            line-height: 1.2;

            &-txt {
                flex: 0 0 85%;
                max-width: 85%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }

            &-value {
    
            }
        }

        &-slidecontainer {
            width: 100%; /* Width of the outside container */

            @media(max-width:768px) {
                margin-bottom: 20px;
            }
          }
    }

    .mb-30 {
        margin-bottom: 30px;
    }

    .mb-55 {
        margin-bottom: 55px;
    }

    

    

    

    .jq-selectbox {
        width: 100%;
        height: 48px;
        overflow: unset;
        border-radius: 3px;
        color: #272733;
        font-size: 1.125rem;
        outline: 0;
        

        &__select {
            height: 100%;
            padding: 0;
            border: none !important;
            box-shadow: none;
            outline: 0;
            border-radius: 3px;
            background: none;
            
            &-text {
                display: flex;
                align-items: center;
                padding-left: 10px;
                height: 100%;
                font-size: rem(18);
                color: #272733;
                background: none;
                background-image: none;
                background-color: rgba(255, 255, 255, 0.85) !important;
                // background: linear-gradient(rgba(255, 255, 255, 0.85)) !important;
                border-radius: 3px;
                outline: 0;
                text-shadow: none;
                
            }
        }

        &__trigger{
            border-left: none;

            &-arrow {
                width: 8px;
                height: 8px;
                transform: rotate(135deg);
                border-top: 1px solid #000;
                border-right: 1px solid #000;
                top: 17px;
                border-left: none;
            }
        }

        &__dropdown {
            max-height: 180px;
            margin-top: -2px;
            padding-top: 2px;
            overflow: hidden;
            overflow-y: auto;
            background-color: #272733;
            border: 1px solid #4a5a68bd;
            border-top: none;
            border-radius: 0;

            ul {

            }

            li {
                padding-right: 10px;
                margin-bottom: 8px;
                color: #ffffff;
                font-size: rem(18);

                &:hover {
                    background-color: #435f6c !important;
                }
            }

            .sel {
                color: #42A9ED;
                // background-color: #3D4050;
            }

            .selected  {
                background-color: #272733 !important;
            }
        }
    }
    
}
    .focused {
    border: none;

    .jq-selectbox__select {
        border: none;
    }
}
.opened  {

    .jq-selectbox__trigger-arrow {
        transform: rotate(315deg);
        top: 22px;
    }

    .jq-selectbox__select-text {
        color: #ffffff;
        background-color: #3D4050 !important;
        border: 1px solid #4a5a68bd;
        border-bottom: none;
    }

    .jq-selectbox__trigger-arrow {
        border-top: 1px solid #ffffff !important;
        border-right: 1px solid #ffffff !important;
    }
}

.jq-selectbox__dropdown::-webkit-scrollbar {
    width: 7px;
    border-radius: 3px;
    background-color:#3d4050;
}

.jq-selectbox__dropdown::-webkit-scrollbar-thumb {
    background-color:#3E9CDC;
    border-radius: 3px;
}

.main-order-input::-webkit-input-placeholder {
    color: #272733;
    opacity:1;
   } 

.jq-selectbox__dropdown ul::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
  .jq-selectbox__dropdown ul {
    -ms-overflow-style: none;
  }
  .jq-selectbox__dropdown ul {
    overflow: -moz-scrollbars-none;
  }

