  /* The slider itself */
  .main-order-slider {
    -webkit-appearance: none;
    width: 100%;
    height: 8px;
    border-radius: 5px;  
    background: rgba(255, 255, 255, 0.7);
    outline: none;
    border: 1px solid #ffffff;
    // opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
  }
  
  .main-order-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%; 
    background: #42A9ED;
    border: 3px solid #286690;
    cursor: pointer;
  }
  
  .main-order-slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #42A9ED;
    border: 3px solid #286690;
    cursor: pointer;
  }